  color: rgb(21 209 233 / var(--tw-text-opacity, 1));
}

.text-lightGrey{
  --tw-text-opacity: 1;
  color: rgb(151 133 128 / var(--tw-text-opacity, 1));
}

.text-orange{
  --tw-text-opacity: 1;
  color: rgb(251 151 24 / var(--tw-text-opacity, 1));
}

.text-white{
  --tw-text-opacity: 1;
  color: rgb(241 225 217 / var(--tw-text-opacity, 1));
}

.opacity-50{
  opacity: 0.5;
}

.blur-sm{
  --tw-blur: blur(4px);
  filter: var(--tw-blur) var(--tw-brightness) var(--tw-contrast) var(--tw-grayscale) var(--tw-hue-rotate) var(--tw-invert) var(--tw-saturate) var(--tw-sepia) var(--tw-drop-shadow);
}

.transition-all{
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.duration-100{
  transition-duration: 100ms;
}

.duration-1000{
  transition-duration: 1000ms;
}

.duration-500{
  transition-duration: 500ms;
}

body {
  background-color: #374151;
}

*{
  box-sizing: border-box;
}

.nav-link {
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 700;
  color: #F3F4F6;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: auto;
}

.nav-link-chiled {
  display: flex;
  flex-direction: row;
  gap: 2.5rem;
}

@media screen and (max-width: 768px) {
  .nav-link-chiled {
    flex-direction: column;
    position:absolute;
    top:6rem;
    box-shadow:2px 2px 10px white;
    width: 94vw;
    gap: 1rem;
    margin-left:7rem;
    text-decoration: none;
  }
}

.nav-triger-btn:hover {
  transition: all 0.6s ease;
  color: cyan;
  border-bottom: 3px solid cyan;
}

.logo {
  background-size: cover;
}

.here-main{
  padding-top: 7rem;
  width: 90%;
  max-width: 1500px;
}

.hexagon{
  animation-duration: 3s;
  top: 6em;
  z-index: -7;
}

.pic{
  top: 6em;
}

.hover\:-translate-y-10:hover{
  --tw-translate-y: -2.5rem;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
}

.hover\:scale-105:hover{
  --tw-scale-x: 1.05;
  --tw-scale-y: 1.05;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
}

.hover\:scale-110:hover{
  --tw-scale-x: 1.1;
  --tw-scale-y: 1.1;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
}

.hover\:bg-darkCyan:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(0 159 179 / var(--tw-bg-opacity, 1));
}

.hover\:bg-orange:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(251 151 24 / var(--tw-bg-opacity, 1));
}

.hover\:text-darkGrey:hover{
  --tw-text-opacity: 1;
  color: rgb(63 68 65 / var(--tw-text-opacity, 1));
}

.hover\:text-orange:hover{
  --tw-text-opacity: 1;
  color: rgb(251 151 24 / var(--tw-text-opacity, 1));
}

.hover\:opacity-0:hover{
  opacity: 0;
}

@media (min-width: 640px){
  .sm\:block{
    display: block;
  }

  .sm\:hidden{
    display: none;
  }

  .sm\:grid-cols-2{
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .sm\:flex-col{
    flex-direction: column;
  }

  .sm\:flex-col-reverse{
    flex-direction: column-reverse;
  }

  .sm\:items-center{
    align-items: center;
  }

  .sm\:self-center{
    align-self: center;
  }

  .sm\:justify-self-center{
    justify-self: center;
  }

  .sm\:text-center{
    text-align: center;
  }

  .sm\:text-2xl{
    font-size: 1.5rem;
    line-height: 2rem;
  }

  .sm\:text-4xl{
    font-size: 2.25rem;
    line-height: 2.5rem;
  }

  .sm\:text-xl{
    font-size: 1.25rem;
    line-height: 1.75rem;
  }
}

@media (min-width: 768px){
  .md\:block{
    display: block;
  }

  .md\:grid-cols-4{
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }

  .md\:flex-row{
    flex-direction: row;
  }

  .md\:flex-row-reverse{
    flex-direction: row-reverse;
  }

  .md\:items-start{
    align-items: flex-start;
  }

  .md\:justify-self-start{
    justify-self: start;
  }

  .md\:justify-self-end{
    justify-self: end;
  }

  .md\:text-left{
    text-align: left;
  }

  .md\:text-right{
    text-align: right;
  }

  .md\:text-2xl{
    font-size: 1.5rem;
    line-height: 2rem;
  }

  .md\:text-3xl{
    font-size: 1.875rem;
    line-height: 2.25rem;
  }

  .md\:text-\[2\.8rem\]{
    font-size: 2.8rem;
  }
}

@media (min-width: 1024px){
  .lg\:block{
    display: block;
  }

  .lg\:hidden{
    display: none;
  }

  .lg\:flex-row{
    flex-direction: row;
  }

  .lg\:text-2xl{
    font-size: 1.5rem;
    line-height: 2rem;
  }

  .lg\:text-6xl{
    font-size: 3.75rem;
    line-height: 1;
  }
}

@media (min-width: 1280px){
  .xl\:text-3xl{
    font-size: 1.875rem;
    line-height: 2.25rem;
  }
}
